@import '../../../../assets/styles/variables';

.Navigation {
  padding: 30px;
  width: $sidebar-width;
  @include mq('tablet') {
    padding: 10px;
  }
}

.NavigationTitle {
  color: $text-color-dark;
  font-size: 16px;
  letter-spacing: 0.02em;
  text-transform: uppercase;
  padding: 20px 0;
}

.NavigationList {
  list-style: none;
  margin: 0;
  padding: 0;
}

.NavigationItem {
  a {
    display: flex;
    align-items: center;
    font-size: 16px;
    text-decoration: none;
    color: $accent;
  }
  span {
    margin-left: 12px;
  }
}